export default {
    layout: "Dum-content",
    data() {
        return {
            tution: {
                name_of_program: "",
                total_fee: "",
                duration: "",
                type: "",


            },            
            programs:'',
            errors: {},
        };
    },
    mounted(){
        this.getProgram();

    },
    methods: {
          getProgram() {
      this.$axios
        .$get("/program/show")
        .then((res) => {
          this.programs = res;          
                  
        })
        .catch((err) => {
          console.log(err);
        });
    },
        addTution() {
            this.$axios
                .$post("/tution/add", this.tution)
                .then((res) => {
                    this.tution = "";
                    this.errors = {};
                    this.$toaster.success(res.message);
                    this.$router.push("/dum-ac/tution");
                })
                .catch((error) => {
                    this.errors = error.response.data.errors;
                });
        },
    },
};
